body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.wait-update-status {
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  background: #FFF59D;
}

@-webkit-keyframes example {
  0%   {opacity: 1;}
  16%  {opacity: 0.75;}
  32%  {opacity: 0.5;}
  48% {opacity: 0;}
  64%  {opacity: 0.5;}
  90%  {opacity: 0.75;}
  100%   {opacity: 1;}
}

@keyframes example {
  0%   {opacity: 1;}
  16%  {opacity: 0.75;}
  32%  {opacity: 0.5;}
  48% {opacity: 0;}
  64%  {opacity: 0.5;}
  90%  {opacity: 0.75;}
  100%   {opacity: 1;}
}



